@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;500&family=Montserrat:wght@100;200;300;400;500&family=Mulish:wght@200;300;400&family=Playfair+Display&family=Quattrocento+Sans:wght@400;700&family=Roboto:wght@100;300;400&display=swap');

/* filter fonts when done! */

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  color: black;
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  scroll-behavior: smooth;
  background-color: #f6f6f6;
}

.active ~ .hover-navlink{
  position: absolute; 
  bottom: 0px; 
  left: 0; 
  top: 30px;
  width: 50%; 
  height: 1px; 
  background-color: #d4d4d4; 
}

a {
  font-family: 'Roboto', sans-serif;
  color: #464644;
  text-decoration: none;
}


.menu-link {
  display: flex;
  align-items: center;
  gap: 10px;
  color: rgb(201, 200, 200);
}

.menu-toggle-container {
  display: flex;
  margin-right: 30px; 
  position: absolute;
  top: 0;
  right: 0;
}

.menu-toggle {
  visibility: hidden;
  z-index: 1;
  border: none;
  background-color: transparent;
  color: #464644;
  font-size: 1.2rem;
}

.menu, .menu-overlay {
  width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}

.menu-overlay {
  background: rgba(0, 0, 0, 1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.menu-close {
  position: absolute;
  right: 0;
  top: 5vh;
  margin-right: 30px;
  font-size: 1.2rem;
  background-color: transparent;
  color: white;
  border: none;
}

.menu-links {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.2rem;
  padding-left: 0;
  z-index: 1;
  gap: 15px;
  letter-spacing: 2px;
}

.menu-overlay .logo {
  margin: 0;
  width: 100%;
  color: white;
  font-size: 4rem;
}


.menu .social-media {
  padding-bottom: 1vh;
}

.navbar-container {
  position: fixed;
  color: black;
  font-weight: 300;
  margin-right: 10vw;
  width: 100vw;
  padding: 5vh 0;
  background-color: #f6f6f6;
  z-index: 1;
}

.navbar {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f6f6f6;
  position: relative;
}

.navbar-links {
  margin-right: 4vw;
  min-width: 350px;
  max-width: 500px;
  flex-grow: 1;
  display: flex;
  justify-content: space-evenly;
}

.navlink {
  position: relative;
}

.navbar-links a {
  border: 0; 
  padding: 10px 0; 
}

.hover-navlink {
  position: absolute; 
  bottom: 0px; 
  left: 0; 
  top: 30px;
  width: 0; 
  height: 1px; 
  background-color: #d4d4d4; 
  transition: 0.2s;
}

.navbar-links a:hover ~ .hover-navlink {
  width: 50%; 
  transition: 0.2s;
}



.logo {
  font-family: "Playfair Display";
  font-size: 2rem;
  margin-left: 70px;
  color: #747474;
}

.search {
  margin-right: 70px;
}

.hero {
  height: 100vh;
  margin-bottom: 16vh;
  width: 100%;
  background-color: #f6f6f6;
  display: flex;
  justify-content:center;
  align-items: center;
}



.hero-mid {
  text-align: center;
  padding: 0 100px;
}

.hero-mid h1, .hero-mid h2 {
  cursor: default;
  border: solid black 1px;
  margin: 0;
  color: rgb(54, 53, 53);
  letter-spacing: .5rem;
  font-size:  1rem;
  font-family: "Lora";
  padding: 15px 20px;
}

.hero-mid h1 {
  border-top: 0;
  font-size: 4rem;
  padding: 20px;
  letter-spacing: 1rem;
}
.hero-mid p {
  cursor: default;
  margin-top: 40px;
}

.shop-link {
  margin-top: 20px;
  color: rgb(54, 53, 53);
  font-size: 1.2rem;
  width: 200px;
  height: 50px;
  background-color: #f6f6f6;
  border: solid black 1px;
  font-family: "Montserrat";
  font-weight: 300;
}
.shop-link:hover {
  cursor: pointer;
}

.hero-img {
  width: 15vw;
  height: 100%;
  transition: transform .2s;
}

.hero-img:hover {
  transform: scale(1.1);
}

.slider-container {
  overflow: hidden;
  height: 70vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f6f6f6;
}

.slider {
  width: 100vw;
  position: relative;
  height: 60vh;
  overflow: hidden;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position:absolute;
  
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.slide-left, .slide-right {
  position: absolute;
  border: none;
  font-size: 2rem;
  color: #747474;
  background-color:transparent;
  cursor: pointer;
  z-index: 1;
}

.slide-left {
  margin-left: 40px;
  left: 0;
}

.slide-right {
  margin-right: 40px;
  right: 0;
}

.slider-section {
  overflow: hidden;
  padding-top: 200px;
  font-family: "Lora";
  background-color: #f6f6f6;
}



.featured-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.featured-header h1 {
  color: black;
}

.featured-img {
  width: 12vw;
}

.featured-title {
  text-align: center;
  color: rgb(66, 66, 66);
}

.featured-price {
  text-align: center;
}

.rooms-container {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40vh;
}

.rooms-container div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60vh;
  width: 33.333333%;
  background-position: bottom 25% right 50%;
  background-repeat: no-repeat;
  background-size: cover;

}

@media only screen and (max-width: 768px) {
  .rooms-container {
    flex-direction: column;
    height: auto;
  }
  .rooms-container div {
    width: 100%;
    height: 60vh;
  }
  .hero-img, .hero-chair-link {
    visibility: hidden;
  }
  .navbar-links {
    visibility: hidden;
    position: absolute;
  }
  .menu-toggle {
    visibility: visible;
  }
  .menu-link {
    visibility: visible;
    opacity: 1;
  }
  
}

.column1 {
  background:  rgba(0, 0, 0, 0.3) url("/public/images/bar.jpg");
  background-blend-mode: darken;
  text-align: center;
}
.column2 {
  background: rgba(0, 0, 0, 0.3) url("/public/images/dining.jpg");
  background-blend-mode: darken;
  text-align: center;
}
.column3 {
  background-image: url("/public/images/living.jpg");
  text-align: center;
}

.room-title {
  color: white;
  font-size: 2.2rem;
  border: none;
}

.rooms-categories {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 0;
  list-style: none;
  font-family: "Montserrat";
  text-align: center;
}


.rooms-categories a li {
  font-size: 0.9rem;
  font-weight: 300;
  color: white;
  z-index: 1;
}

.room-button {
  letter-spacing: 4px;
  color: white;
  background: transparent;
  border: solid white 1px;
  font-weight: bold;
  padding: 15px 30px;
}

.column h2, .column button {
  border: none;
}

.footer {
  margin-top: 100px;
  padding-bottom: 40px;
  background-color: #ebebeb;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.footer-title {
  color: rgb(57, 57, 57);
  font-family: "Lora";
  letter-spacing: 4px;
}

.footer-info, .footer-collections, .footer-contact {
  max-width: 20%;
  min-width: 200px;
}

.footer-newsletter {
  flex-grow: 1;
  min-width: 200px;
  max-width: 30%;
  display: flex;
  flex-direction: column;
}


.newsletter-signup {
  justify-self: flex-end;
  display: flex;
  flex-direction: row;
  float: left; 
  width: 100%;
  margin: 5% 0;
  position: relative;
}

.email-input {
  width: 100%;
  font-size: 1rem;
  opacity: 0.5;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 7px 0; 
  border-bottom: 1px solid #ccc;
  z-index: 1;
}

.email-input ~ .focus-animation {
  position: absolute; 
  bottom: 0; 
  left: 0; 
  width: 0; 
  height: 2px; 
  background-color: #3399FF; 
  transition: 0.4s;
  z-index: 1;
}

.email-input:focus ~ .focus-animation {
  width: 100%; 
  transition: 0.4s;
}

.email-valid {
  color: green;
}

.email-invalid {
  color: red;
}

.newsletter-signup button {
  background-color: transparent;
  border: none;
  font-size: 1.5rem;
  color:rgb(57, 57, 57);
  position: absolute;
  right: 0;
  z-index: 1
}

.social-media {
  display: flex;
  justify-content: space-evenly;
  font-size: 1.8rem;
  color: #464644;
}


.addToWishlist:hover, .removeFromWishlist:hover, .shop-link:hover, .hero-img:hover, .room-button:hover {
  cursor: pointer;
}

.shop {
  background-color:#f6f6f6;
}

.shop-header, .cart-header {
  margin-bottom: 5vh;

  padding: 5% 0;
  display: flex;
  color: white;
  align-items: center;
  padding-left: 13vw;
  gap: 20px;
  font-size: 2rem;
  font-weight: 300;
  height: 50vh;
  
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
}

.shop-header {
  background-image: url(/public/images/shop-bg.jpg);
}

.cart-header {
  background-image: url(/public/images/cart-bg.jpg);
  background-position: 90%;
  height: 35vh;
}

.individual-price {
  display: flex;
  position: relative;
  width: 15%;
}


.cart-discount {
  position: absolute;
  left: 50px;
}

.shop-header:hover, .cart-header:hover {
  cursor:default;
}

.shop-header span, .cart-header span {
  background-color: white;
  height: 1px;
  width: 30px;
}

.item-section {
  display: flex;
  justify-content: flex-end;
}

.shop-sidebar {
  display: flex;
  gap: 15px;
  flex-direction: column;
  align-items: center;
  text-align: left;
  width: 20vw;
  margin-top: 210px;
  padding-left: 3vw;
}

.shop-sidebar button {
  cursor: pointer;
  align-self: flex-start;
  text-align: left;
  background-color: transparent;
  border: none;
  font-family: "Roboto";
  font-size: 1rem;
  font-weight: 300;
  color:#868686;
  letter-spacing: 1px;
  padding-bottom: 10px;
}

.select-styling {
  position : relative;
  align-self: flex-start;
}

.chair-sort {
  cursor: pointer;
  appearance: none;
  font-family: "Roboto";
  font-weight: 300;
  color: #868686;
  font-size: inherit;
  background-color: transparent;  
  border: solid 1px #a3a3a3;
  padding: .8rem 2.5rem .8rem 1.5rem;
  margin: 0;
}

.chair-sort:focus {
  appearance: none;
}

.select-arrow {
  position: absolute;
  pointer-events: none;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  width: 20%;
  height: 95%;
}

.conts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 3vw;
}

.filter-title {
  padding-bottom: 50px;
  font-size: 1.8rem;
  font-weight: 300;
  color: #474645;
  margin-bottom: 100px;
  text-align: center;
  letter-spacing: 2px;
  position: relative;
}

.filter-title::after {
    width: 20px;
    height: .5px;
    position: absolute;
    left: 50%;
    bottom: 0;
    background: #464644;
    transform: translateX(-50%);
    content: '';
}


.shop-items {
  width: 70vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 20px;
}

.shop-item {
  width: 40%;
  height: 20vw;
  position: relative;
  border: solid black 1px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.chair {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.chair-hover > * {
  transform: translateY(50%);
  transition: transform 0.5s;
}

.chair-hover {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  color: rgb(218, 218, 218);
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: opacity 0.5s;
}

.chair-hover:hover > * {
  transform: translateY(0);
}

.chair-hover:hover {
  opacity: 1;
}

.chair-mid {
  font-weight: 300;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5%;
}



.chair-mid a {
  color: white;
  border: solid rgb(218, 218, 218) 1px;
  padding: .5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.addToWishlist, .removeFromWishlist {
  align-self: flex-end;
  font-size: 1.3rem;
  padding-right: 5%;
}

.wishlistItem, .cartItem {
  border: solid 1px black;
}

.media-title {
  text-align: center;
  visibility: hidden;
  position: absolute;
  left: 45%;
  right: 50%;
  top: 100%;
}

@media only screen and (max-width: 970px) {

  .shop-items {
    width: 90vw;
    gap: 60px;
  }

  .shop-item {
    min-width: 200px;
    max-width: 300px;
    
  }

  .item-section {
    flex-direction: column;
  }

  .shop-sidebar {
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    width: 95%;
    justify-content: space-around;
    margin: 40px 0;
    padding-bottom: 20px;
  }

  .shop-sidebar button {
    margin: auto 0;
    padding: 0;
  }

  .shop-sidebar :nth-child(5) { order: 1; }

  .filter-title {
    display: none;
  }

  .media-title {
    visibility: visible;
  }
}

@media only screen and (max-width: 600px) {
  .shop-items {
    flex-direction: column;
    align-items: center;
  }

  .shop-item {
    width: 80vw;
    height: 40vw;
  }
  .contact-column {
    flex-direction: column;
  }
}

.info-container {
  padding-top: 15vh;
  display: flex;
  justify-content: center;
  gap: 100px;
  width: 80%;
  margin: 0 auto;
}

.image-container {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.detailsImage {
  min-width: 200px;
  width: 20vw;
  height: auto;
  padding: 5% 10%;
  min-width: 350px;
  background-color: #ebebeb;
}

.chair-info {
width: 100%;
display: flex;
flex-direction: column;
align-items: flex-start;
}

.chair-info h1 {
  font-weight: 400;
}

.chair-availability {
  color: #6a6a6a;
  margin-top: 10px;
}

.chair-description {
  width: 100%;
  font-weight: 100;
  line-height: 30px;
  margin-top: 30px;
}

.priceDiscounted {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
}

.discountPrice {
  color: rgb(199, 147, 147);
  font-size: 2rem;
  margin: 0;
}

.originalPrice {
  text-decoration: line-through;
  margin: 0;
}

.no-discount {

  color: black;
}

.originalPrice, .no-discount {
  font-size: 1.5rem;
  margin: 0;
}

.quantityManager {
  margin-top: auto;
  display: flex;
  gap: 20px;
}

.quantityManager button {
  background-color: transparent;
  border: none;
  font-size: 1.2rem;
  color: #868686;
  font-weight: 200;
  cursor: pointer;
}

.basket-container {
  display: flex;
}

.basket-notification {
  background-color: rgb(104, 159, 104);
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  color: white;
  width: 50px
}

.add-to-basket {
  overflow: hidden;
  background-color: transparent;
  border: solid 1px #868686;;
  padding: 0 40px;
  cursor: pointer;
  color: #868686;
  margin: 0;
}

.button-slidetext {
  display: none;
}

.chair-tabs {
  margin-top: 10vh;
  padding: 20px 0;
  min-height: 20vh;
  width: 100vw;
  background-color: #ebebeb;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
}

.chair-tabs span {
  background-color: #a3a3a3;
  height: 1px;
  width: 70vw;
  margin-bottom: 20px;
}

.react-tabs__tab--selected {
  color: black;
}

.itemInfo {
  display: flex;
  width: 70vw;
  list-style: none;
  gap: 50px;
  cursor: pointer;
}

.itemInfo li {
  font-size: 1.2rem;
  font-family: "Lora";
  letter-spacing: 4px;
}

.item-description h3, .additional-info h3 {
  font-weight: 400;
  font-size: 1.2rem;
}

.additional-info-container {
  width: 70vw;
}

.item-description {
  line-height: 30px;
}

.item-description, .item-description p, .additional-info {
  width: 70%;
}

.column-separator {
  background-color: #d4d4d4;
  height: .5px;
  width: 100%;
  margin: 10px 0;
}

.info-column {
  color: #5d5d5d;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 970px) {
  .info-container {
    flex-direction: column;
    margin: 0 auto;
    width: 100vw;  
  }
  .detailsImage {
    width: 100%;
    height: auto;
  }
  .image-container {
    width: 80%;
    justify-content: center;
    margin: 0 auto;
  }
  .additional-info, .item-description p {
    width: 100%;
  }
  .itemInfo {
    width: 90%;
    padding: 0;
    justify-content: space-evenly;
  
  }
  .chair-info {
    width: 80%;
    margin: 0 auto
  }
  .chair-tabs {
    width: 80vw;
  }
}

@media only screen and (max-width: 600px) {
  .itemInfo {
    flex-direction: column;
    align-items: center;
  }
  .itemInfo li {
    border: solid black 1px;
    padding: 10px;
  }
  
}

.contact-container {
  padding-top: 20vh;
  width: 70vw;
  margin-left: 10%;
  text-align: left;
}

.contact-column {
  display: flex;
  justify-content: space-between;
  border-bottom:solid 1px #d4d4d4 ;
}

.contact-column div {
  min-width: 25%;
}

.contact-column h2 {
  font-weight: 400;
}

.cart-header-title {
  margin-left: 10vw;
  color: #464644;
}

.cart-items {
  width: 60%;
}

@media only screen and (max-width: 1250px){
  .cart-container {
    flex-direction: column
  }
  .cart-items {
    width: 100%;
  }
}

.cart-container {
  display: flex;
  justify-content: space-evenly;
  font-size: .8rem;
  width: 80vw;
  margin: 0 auto;
}

.cart-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 0;
  border-bottom: solid #ebebeb 1px;
}

.cart-column {
  display: flex;
  align-items: center;
  gap: 20px;
}

.cart-img {
  background-color: #ebebeb;
  width: 100px;
}

.cart-title {
  font-weight: 400;
  color: rgb(45, 45, 45);
  font-size: 1rem;
}

.remove-bundle {
  font-size: 1.2rem;
  cursor: pointer;
}

.basket-itemcounter {
  padding: 3px 15px;
  display: flex;
  gap: 15px;
  align-items: center;
  background-color: transparent;
  border: solid #a3a3a3 1px;
  font-weight: 200;
  
}

.basket-itemcounter button {
  background-color: transparent;
  border: none;
  color: #626262;
  cursor: pointer;
}

.basket-itemcounter h3 {
  padding-right: 10px;
  font-weight: 200;
  font-size: .8rem;
}

.cart-total {
  background-color: #ebebeb;
  padding: 50px;
  display: flex;
  flex-direction: column;
  height: 330px;
}

.shipping, .subtotal, .total-price {
  display: flex;
  align-items: center;
}

.total-price {
  border-top: solid #a3a3a3 1px;
  margin-top: auto;
  display: flex;
  justify-content: space-between;
}

.total-price p {
  
  font-size: 2rem;
  color: black;
}

.cart-total {
  min-width: 240px;
}

.cart-total h2{
  color: #464644;
  letter-spacing: 2px;
  font-size: 1.5rem;
}

.cart-total h3 {
  width: 30%;
  font-weight: 400;
  font-size: .9rem;
  color: #464644;
}

.checkout {
  background-color: black;
  color: white;
  border: none;
  padding: 13px;
}

@media only screen and (max-width: 768px) {
  .basket-itemcounter {
    border: none;
  }
  .basket-itemcounter h3 {
    display: none;
  }
}

@media only screen and (max-width: 600px)  {
  .individual-price {
    display: none;
  }
  .cart-img {
    display: none;
  }
  .cart-total {
    padding: 30px 20px;
  }
}

.wishlist-items {
  width: 70vw;
  margin: 0 auto;
}

.wishlist-item {
  display: flex;
  justify-content: space-between;
  border-bottom: solid #ebebeb 1px;
  padding: 20px 0;
}

.wishlist-right {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.wishlist-basket {
  color: #474645;
  font-weight: 600;
  font-size: .8rem;
}

.wishlist-discount {
  position: absolute;
  right: 50px;
}

